.Layout__container {
  width: 100%;
  height: 100%;
  min-height: 900px;
  background-color: var(--light-green);
  text-align: center;
}

.Layout__header {
  height: 75px;
  background-color: var(--medium-green);
}

.Layout__child {
  height: 100%;
  width: 80%;
  margin: 0 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.Layout__brand {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.Layout__button {
  -ms-flex-item-align: center;
      align-self: center;
}

.Layout__title {
  font-size: 30px;
  color: white;
  margin-left: 10px;
}

.Layout__button {
  background: none;
  border: none;
  color: white;
}

.Layout__body {
  width: 80%;
  min-height: 900px;
  margin: 0 auto;
  background-color: #e2f3ea;
}



